body
{
    background-color: rgb(2, 2, 26);
}

.container
{
    width: 400px;
    height: 200px;
    background-color: rgb(32, 32, 32);
    border-radius: 25px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    margin-top: 200px;

    box-shadow: inset 2px 3px 5px rgb(97, 183, 229), inset -2px -3px 20px rgb(7, 7, 54);
}

.counterLabel
{
    font-size: 50px;
    color: wheat;
    font-weight: bold;
}

.btns_container
{
    width: 300px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row;
}

.btns_container button
{
    width: 70px;
    height: 70px;
    border: none;
    border-radius: 25px;
    margin-left: 15px;
    margin-right: 15px;

    font-size: 40px;
    background-color: rgb(34, 49, 182);
    color: lightblue;
    font-weight: bold;
    box-shadow: 1px 1px 10px rgb(13, 20, 74),
    inset 1px 1px 5px lightblue, inset -1px -1px 10px rgb(3, 3, 32);
}